<template>
    <div class="barCurrency" ref="barCurrency">

    </div>
</template>

<script>
    let option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#999'
          }
        }
      },
      grid: {
        containLabel: true,
        top : 40,    //距离容器上边界40像素
        bottom: 30,   //距离容器下边界30像素
        left:30,
        right:30,
      },
      toolbox: {
        top:6,
        right: 100,
        feature: {
          // dataView: {show: true, readOnly: false},
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: true},
          saveAsImage: {show: true}
        }
      },
      legend: {
        show:false,
        data: [ '次数', '成功次数'],
        selected:{
          '次数':true,
          '成功次数':true,
        }
      },
      xAxis: [
        {
          type: 'category',
          data: [],
          axisPointer: {
            type: 'shadow'
          }
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '次数',
          // min: 0,
          // max: 250,
          // interval: 50,
          axisLabel: {
            formatter: '{value} 次'
          }
        },
        {
          type: 'value',
          name: '成功次数',
          // min: 0,
          // max: 25,
          // interval: 5,
          axisLabel: {
            formatter: '{value}'
          }
        }
      ],
      series: [
        {
          name: '次数',
          type: 'bar',
          data: [],
          color:'#49c6ec'
        },
        {
          name: '成功次数',
          type: 'line',
          yAxisIndex: 1,
          data: [],
          color:'#ea8d45'
        }
      ]
    }
  export default {
    name: "barCurrency",
    data(){
      return{
        chartsOption:option
      }
    },
    mounted() {
      this.init()
    },
    methods: {
      init(seriesBar0=[],seriesLine1=[],dataX=[]){
        if (seriesBar0.length>0){
          this.chartsOption.series[0].data = seriesBar0;
        }
        if (seriesLine1.length>0){
          this.chartsOption.series[1].data = seriesLine1;
        }
        this.chartsOption.xAxis[0].data = dataX
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.barCurrency)
        this.mychart.setOption(this.chartsOption);
        let mychart = echarts.init(this.$refs.barCurrency, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
        //点击事件
        let _this = this
        this.mychart.off('click');
        this.mychart.on('click', function (params) {
          window.console.log(params)
          _this.lineDialogVisible = true
        })
      }

    }
  }
</script>

<style scoped>
    .barCurrency{
        height: 100%;
        width: 100%;
    }

</style>